* {
    margin: 0;
    padding: 0;
}

html, body {
    max-width: 100%;
    overflow-x: hidden;
    background-color: #303030;
}

.map {
    width: 100vw;
    height: 60vw;
}

.map_image {
    width: 80vw;
    height: 45vw;
    margin-left: 10vw;
    margin-top: 7.5vw;
    background-color: transparent;
    object-fit: scale-down;
}
